.generated-image-wrap::before {
  background-image: url("data:image/png;base64,");
  content: "";
  position: absolute;
  top: 0;
  right: 2px;
  z-index: -1;
  background-size: cover;
  opacity: 0.5;
  height: 90px;
  width: 135px;
}

.generated-image-wrap::after {
  background-image: url("data:image/png;base64,");
  content: "";
  position: absolute;
  bottom: 2px;
  right: -10px;
  z-index: 0;
  background-size: cover;
  opacity: 0.1;
  height: 150px;
  width: 205px;
  filter: invert(100%);
  -webkit-filter: invert(100%);
}

.wave {
  background-image: url("data:image/png;base64,");
  position: absolute;
  top: 0;
  left: 0px;
  z-index: 0;
  background-size: cover;
  opacity: 0.2;
  height: 100%;
  width: 100%;
}
